import React, { Children, useEffect } from "react";
import DialogWindow from "./Dialog";
import { usePizzaContext } from "../providers/PizzaDataProvider";

function OrderItem({ pizzaData }) {
	return (
		<div className="order-item">
			<img
				src={require("../" +
					pizzaData.icon.slice(0, pizzaData.icon.length - 3) +
					"png")}
				alt="pizza"
			/>
			<h3>{pizzaData.title}</h3>

			<span className="orange">{pizzaData.amount} .</span>
		</div>
	);
}

function DeliveryForm({ dialogOpen, handleDialogClose }) {
	const { bucketList, setBucketList, totalPrice } = usePizzaContext();
	const [userData, setUserData] = React.useState({
		name: "",
		phone: "",
		email: "",
		city: "none",
		address: "",
		dontCall: false,
		payMethod: "",
		isNameTouched: false,
		isEmailTouched: false,
		isPhoneTouched: false,
		isAddressTouched: false,
	});

	const [formSubmitted, setFormSubmitted] = React.useState(false);

	const SuccessAlert = () => {
		return (
			<div className="row">
				<div className="alert alert-success" role="alert">
					 !  '.
				</div>
			</div>
		);
	};

	const ErrorMessageName = ({ children }) => {
		return <div className="invalid-feedback">{children}</div>;
	};
	const getIsPhoneValid = () => {
		const pattern1 = /^\+380\d{9}$/;
		const pattern2 = /^0\d{9}$/;
		const trimmedPhone = userData.phone.replace(/\s+/g, "");
		// Use the test() method to check if the phoneNumber matches the pattern
		return pattern1.test(trimmedPhone) || pattern2.test(trimmedPhone);
	};
	const getIsEmailValid = () => {
		const emailPattern = /^[A-Za-z0-9._%-]+@[A-Za-z0-9.-]+\.[A-Za-z]{2,4}$/;

		// Use the test() method to check if the email matches the pattern
		return emailPattern.test(userData.email.trim());
	};
	const getIsCityValid = () => {
		// Use the test() method to check if the email matches the pattern
		return userData.city !== "none";
	};

	const getIsFormValid = () => {
		return (
			userData.name &&
			getIsPhoneValid() &&
			getIsEmailValid() &&
			getIsCityValid()
		);
	};

	function renderBucketItems() {
		//console.log('rendering intros: ', intros)
		return bucketList.map((pizza, index) => (
			<OrderItem pizzaData={pizza} key={pizza.title} />
		));
	}


	function submitOrder(e) {
		e.preventDefault();
		if (getIsFormValid) {
			setUserData({
				name: "",
				phone: "",
				email: "",
				city: "none",
				address: "",
				dontCall: false,
				payMethod: "",
				isNameTouched: false,
				isEmailTouched: false,
				isPhoneTouched: false,
			});
            setFormSubmitted(true);
			
		}
	}
    useEffect(()=>{
        if (!dialogOpen && formSubmitted) {
        setFormSubmitted(false);
        setBucketList([])
    }
    }, [dialogOpen, formSubmitted])
    

	return (
		<DialogWindow
			title=" "
			open={dialogOpen}
			handleClose={handleDialogClose}
		>
			<div className="dialog-content">
            <div className="order">
							<h2> :</h2>
							<div className="order-container">{renderBucketItems()}</div>
						</div>

				{formSubmitted ? (
					<SuccessAlert />
				) : (
					<>
						
						<form className="delivery-form col-8 needs-validation">
							<div className="row">
								<div className="col-12">
									<label htmlFor="name" className="form-label">
										 ':
									</label>
									<input
										autoComplete="name"
										className={`form-control  add-intro-form-input ${
											userData.isNameTouched
												? userData.name
													? "is-valid"
													: "is-invalid"
												: ""
										}`}
										type="text"
										name="name"
										id="name"
										placeholder="  '"
										required
										value={userData.name}
										onChange={(e) => {
											setFormSubmitted(false);
											setUserData({
												...userData,
												isNameTouched: true,
												name: e.target.value,
											});
										}}
									/>
									{userData.isNameTouched && !userData.name && (
										<ErrorMessageName>  !</ErrorMessageName>
									)}
								</div>
							</div>
							<div className="row">
								<div className="col-md-5 col-12">
									<label htmlFor="phone" className="form-label">
										 :
									</label>
									<input
										autoComplete="tel"
										type="tel"
										className={`form-control  add-intro-form-input ${
											userData.isPhoneTouched
												? getIsPhoneValid()
													? "is-valid"
													: "is-invalid"
												: ""
										}`}
										name="phone"
										id="phone"
										placeholder="+380000000000"
										value={userData.phone}
										onChange={(e) => {
											setFormSubmitted(false);
											setUserData({
												...userData,
												isPhoneTouched: true,
												phone: e.target.value,
											});
										}}
									/>
									{userData.isPhoneTouched && !getIsPhoneValid() && (
										<ErrorMessageName>
											   !
										</ErrorMessageName>
									)}
								</div>
								<div className="col-md-7  col-12">
									<label htmlFor="email" className="form-label">
										 :
									</label>
									<input
										autoComplete="email"
										type="email"
										className={`form-control  add-intro-form-input ${
											userData.isEmailTouched
												? getIsEmailValid()
													? "is-valid"
													: "is-invalid"
												: ""
										}`}
										name="email"
										id="email"
										placeholder="example@example.com"
										value={userData.email}
										onChange={(e) => {
											setFormSubmitted(false);
											setUserData({
												...userData,
												isEmailTouched: true,
												email: e.target.value,
											});
										}}
									/>
									{userData.isEmailTouched && !getIsEmailValid() && (
										<ErrorMessageName>
											   !
										</ErrorMessageName>
									)}
								</div>
							</div>
							<div className="row">
								<div className="col-12">
									<label htmlFor="city" className="form-label">
										 :
									</label>
									<select
										autoComplete="address-level2"
										className={`form-control  add-intro-form-input ${
											getIsCityValid() ? "is-valid" : ""
										}`}
										id="city"
										value={userData.city}
										onChange={(e) => {
											setUserData({ ...userData, city: e.target.value });
										}}
										required
									>
										<option disabled value="none">
											...
										</option>
										<option value="kyiv"></option>
										<option value="lviv"></option>
										<option value="kharkiv"></option>
										<option value="dnipro"></option>
										<option value="odesa"></option>
										<option value="chernihiv"></option>
									</select>
								</div>
							</div>
							<div className="row">
								<div className="col-12">
									<label htmlFor="address" className="form-label">
										 :
									</label>
									<input
										autoComplete="street-address"
										className={`form-control  add-intro-form-input ${
											userData.isAddressTouched
												? userData.address
													? "is-valid"
													: "is-invalid"
												: ""
										}`}
										type="text"
										name="address"
										id="address"
										placeholder=".   1,  1, . 100"
										required
										value={userData.address}
										onChange={(e) => {
											setFormSubmitted(false);
											setUserData({
												...userData,
												isAddressTouched: true,
												address: e.target.value,
											});
										}}
									/>
									{userData.isAddressTouched && !userData.address && (
										<ErrorMessageName>  !</ErrorMessageName>
									)}
								</div>
							</div>
							<div className="row">
								<div className="col-12">
									<div className="form-check">
										<input
											className="form-check-input"
											type="checkbox"
											value=""
											id="dontCall"
											checked={userData.dontCall}
											onChange={() =>
												setUserData({
													...userData,
													dontCall: !userData.dontCall,
												})
											}
										/>
										<label className="form-check-label" htmlFor="dontCall">
											 
										</label>
									</div>
								</div>
							</div>
							<div className="row">
								<h3>
									  <span className="orange">{totalPrice}.</span>
								</h3>
							</div>

							<div className="row justify-content-center">
								<button
									className="orange-btn"
									name="submit"
									type="submit"
									disabled={!getIsFormValid()}
									onClick={submitOrder}
								>
									 
								</button>
							</div>
						</form>
					</>
				)}
			</div>
		</DialogWindow>
	);
}

export default DeliveryForm;
